import React from 'react';
import PropTypes from 'prop-types';
import * as AntDesign from 'antd';

import {dropDashProps} from '../utils';

/**
 * Handling the overall sidebar of a page.
 */
const Space = props => <AntDesign.Space {...dropDashProps(props)} />

Space.defaultProps = {};

Space.propTypes = {
    /**
     * The ID of this component, used to identify dash components in callbacks.
     * The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,

    /**
     * The children of this component
     */
    children: PropTypes.node,

    /**
     * Container className
     */
    className: PropTypes.string,

    /**
     * Align items
     */
    align: PropTypes.oneOf(['start', 'end', 'center', 'baseline']),

    /**
     * The space direction
     */
    direction: PropTypes.oneOf(['horizontal', 'vertical']),

    /**
     * The space size
     */
    size: PropTypes.oneOfType([
        PropTypes.oneOf(['small', 'middle', 'large']),
        PropTypes.number,
    ]),

    /**
     * Set split
     */
    split: PropTypes.node,
};

export default Space;
