import React from 'react';
import PropTypes from 'prop-types';
import * as AntDesign from 'antd';

import {dropDashProps} from '../utils';

/**
 * 24 Grids System.
 */
const GridCol = props => <AntDesign.Col {...dropDashProps(props)} />

GridCol.defaultProps = {};

GridCol.propTypes = {
    /**
     * The ID of this component, used to identify dash components in callbacks.
     * The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,

    /**
     * The children of this component
     */
    children: PropTypes.node,

    /**
     * Container className
     */
    className: PropTypes.string,

    /**
     * The number of cells to offset Col from the left
     */
    offset: PropTypes.number,

    /**
     * Raster order
     */
    order: PropTypes.number,

    /**
     * The number of cells that raster is moved to the left
     */
    pull: PropTypes.number,

    /**
     * The number of cells that raster is moved to the right
     */
    push: PropTypes.number,

    /**
     * Raster number of cells to occupy, 0 corresponds to display: none
     */
    span: PropTypes.number,

    /**
     * screen < 576px and also default setting, could be a span value or an object containing above props	number | object	-
     */
    xs: PropTypes.number,

    /**
     * screen  576px, could be a span value or an object containing above props	number | object	-
     */
    sm: PropTypes.number,

    /**
     * screen  768px, could be a span value or an object containing above props	number | object	-
     */
    md: PropTypes.number,

    /**
     * screen  992px, could be a span value or an object containing above props	number | object	-
     */
    lg: PropTypes.number,

    /**
     * screen  1200px, could be a span value or an object containing above props	number | object	-
     */
    xl: PropTypes.number,

    /**
     * screen  1600px, could be a span value or an object containing above props	number | object
     */
    xxl: PropTypes.number,
};

export default GridCol;
