import React from 'react';
import PropTypes from 'prop-types';
import * as AntDesign from 'antd';

import {wrapEditable} from '../utils';

/**
 * Basic text writing, including headings, body text, lists, and more.
 */
const TypographyText = props => {
    const {
        children,
        editable,
        setProps,
        loading_state,
        ...otherProps
    } = props;

    const [editableStr, editableProps] = wrapEditable(children, editable, setProps);

    return (
        <AntDesign.Typography.Text
            editable={editableProps}
            {...otherProps}
        >
            {editableStr}
        </AntDesign.Typography.Text>

    );
}

TypographyText.defaultProps = {};

TypographyText.propTypes = {
    /**
     * The ID of this component, used to identify dash components in callbacks.
     * The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,

    /**
     * The children of this component
     */
    children: PropTypes.node,

    /**
     * Container className
     */
    className: PropTypes.string,

    /**
     * Code style
     */
    code: PropTypes.bool,

    /**
     * Whether to be copyable, customize it via setting an object
     */
    copyable: PropTypes.oneOfType([PropTypes.bool, PropTypes.object]),

    /**
     * Deleted line style
     */
    delete: PropTypes.bool,

    /**
     * Disabled content
     */
    disabled: PropTypes.bool,

    /**
     * If editable. Can control edit state when is object
     */
    editable: PropTypes.oneOfType([PropTypes.bool, PropTypes.object]),

    /**
     * Display ellipsis when text overflows, can configure rows and expandable by using object
     */
    ellipsis: PropTypes.oneOfType([PropTypes.bool, PropTypes.object]),

    /**
     * Keyboard style
     */
    keyboard: PropTypes.bool,

    /**
     * Marked style
     */
    mark: PropTypes.bool,

    /**
     * Bold style
     */
    strong: PropTypes.bool,

    /**
     * Content type
     */
    type: PropTypes.oneOf(['secondary', 'success', 'warning', 'danger']),

    /**
     * Underlined style	boolean	false
     */
    underline: PropTypes.bool,
};

export default TypographyText;
