import React from 'react';
import PropTypes from 'prop-types';
import * as AntDesign from 'antd';

import {dropDashProps} from '../utils';

/**
 * 24 Grids System.
 */
const GridRow = props => <AntDesign.Row {...dropDashProps(props)} />

GridRow.defaultProps = {};

GridRow.propTypes = {
    /**
     * The ID of this component, used to identify dash components in callbacks.
     * The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,

    /**
     * The children of this component
     */
    children: PropTypes.node,

    /**
     * Container className
     */
    className: PropTypes.string,

    /**
     * Vertical alignment
     */
    align: PropTypes.oneOf(['top', 'middle', 'bottom']),

    /**
     * Spacing between grids, could be a number or a object like { xs: 8, sm: 16, md: 24}.
     * Or you can use array to make horizontal and vertical spacing work at the same time [horizontal, vertical]
     */
    gutter: PropTypes.oneOfType([PropTypes.number, PropTypes.object, PropTypes.array]),

    /**
     * Horizontal arrangement
     */
    justify: PropTypes.oneOf(['start', 'end', 'center', 'space-around', 'space-between']),
};

export default GridRow;
