import React from 'react';
import PropTypes from 'prop-types';
import * as AntDesign from 'antd';

import Icon from './Icon.react';

/**
 * To trigger an operation.
 */
const Button = props => {
    const {
        n_clicks,
        n_clicks_timestamp,
        icon,
        tooltip,
        setProps,
        loading_state,
        ...otherProps
    } = props;

    const button = (
        <AntDesign.Button
            icon={icon && <Icon name={icon} />}
            onClick={e => setProps && setProps({
                n_clicks: n_clicks + 1,
                n_clicks_timestamp: Date.now()
            })}
            {...otherProps}
        />
    );

    return tooltip ? (
        <AntDesign.Tooltip title={tooltip}>{button}</AntDesign.Tooltip>
    ) : button;
}

Button.defaultProps = {
    n_clicks: 0,
    n_clicks_timestamp: -1,
};

Button.propTypes = {
    /**
     * The ID of this component, used to identify dash components in callbacks.
     * The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,

    /**
     * The children of this component
     */
    children: PropTypes.node,

    /**
     * An integer that represents the number of times
     * that this element has been clicked on.
     */
    n_clicks: PropTypes.number,

    /**
     * Use of *_timestamp props has been deprecated in Dash in favour of dash.callback_context.
     * See "How do I determine which Input has changed?" in the Dash FAQs https://dash.plot.ly/faqs.
     *
     * An integer that represents the time (in ms since 1970)
     * at which n_clicks changed. This can be used to tell
     * which button was changed most recently.
     */
    n_clicks_timestamp: PropTypes.number,

    /**
     * Option to fit button width to its parent width
     */
    block: PropTypes.bool,

    /**
     * Set the danger status of button
     */
    danger: PropTypes.bool,

    /**
     * 	Disabled state of button
     */
    disabled: PropTypes.bool,

    /**
     * Make background transparent and invert text and border colors
     */
    ghost: PropTypes.bool,

    /**
     * Redirect url of link button
     */
    href: PropTypes.string,

    /**
     * 	Set the original html type of button, see: https://developer.mozilla.org/en-US/docs/Web/HTML/Element/button#attr-type
     */
    htmlType: PropTypes.oneOf(['submit', 'reset', 'button']),

    /**
     * Set the icon name of button
     */
    icon: PropTypes.string,

    /**
     * Set the tooltip title of button
     */
    tooltip: PropTypes.string,

    /**
     * Set the loading status of button
     */
    loading: PropTypes.oneOfType([PropTypes.bool, PropTypes.number]),

    /**
     * Can be set button shape
     */
    shape: PropTypes.oneOf(['circle', 'round']),

    /**
     * Set the size of button
     */
    size: PropTypes.oneOf(['large', 'middle', 'small']),

    /**
     * Same as target attribute of a, works when href is specified
     */
    target: PropTypes.string,

    /**
     * Can be set to primary / ghost / dashed / link / text / default
     */
    type: PropTypes.oneOf(['primary', 'ghost', 'dashed', 'link', 'text', 'default']),
};

export default Button;
