import React from 'react';
import PropTypes from 'prop-types';
import * as AntDesign from 'antd';

import {dropDashProps} from '../utils';

/**
 * Handling the overall sidebar of a page.
 */
const LayoutSider = props => <AntDesign.Layout.Sider {...dropDashProps(props)} />

LayoutSider.defaultProps = {};

LayoutSider.propTypes = {
    /**
     * The ID of this component, used to identify dash components in callbacks.
     * The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,

    /**
     * The children of this component
     */
    children: PropTypes.node,

    /**
     * Container className
     */
    className: PropTypes.string,

    /**
     * To set the current status
     */
    collapsed: PropTypes.bool,

    /**
     * Width of the collapsed sidebar, by setting to 0 a special trigger will appear
     */
    collapsedWidth: PropTypes.number,

    /**
     * Whether can be collapsed
     */
    collapsible: PropTypes.bool,

    /**
     * Reverse direction of arrow, for a sider that expands from the right
     */
    reverseArrow: PropTypes.bool,

    /**
     * Color theme of the sidebar
     */
    theme: PropTypes.oneOf(['light', 'dark']),

    /**
     * Specify the customized trigger, set to null to hide the trigger
     */
    trigger: PropTypes.node,

    /**
     * Width of the sidebar
     */
    width: PropTypes.number,
};

export default LayoutSider;
