import {useState} from 'react';
import {omit} from 'ramda';

export const dropDashProps = props => omit(['setProps', 'loading_state'], props)

export function wrapEditable(children, editable, setProps) {
    if (!!editable) {
        const [editableStr, setEditableStr] = useState(children);
        return [editableStr, {
            onChange: editableStr => {
                setEditableStr(editableStr);
                setProps && setProps({ children: editableStr });
            }
        }]
    }
    return [children, editable]
}
