import React from 'react';
import PropTypes from 'prop-types';
import * as AntDesign from 'antd';

import {dropDashProps} from '../utils';

/**
 * Handling the overall sidebar of a page.
 */
const Divider = props => <AntDesign.Divider {...dropDashProps(props)} />

Divider.defaultProps = {};

Divider.propTypes = {
    /**
     * The ID of this component, used to identify dash components in callbacks.
     * The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,

    /**
     * The children of this component
     */
    children: PropTypes.node,

    /**
     * Container className
     */
    className: PropTypes.string,

    /**
     * Whether line is dashed
     */
    dashed: PropTypes.bool,

    /**
     * The position of title inside divider
     */
    orientation: PropTypes.oneOf(['left', 'right', 'center']),

    /**
     * Divider text show as plain style
     */
    plain: PropTypes.bool,

    /**
     * The direction type of divider
     */
    type: PropTypes.oneOf(['horizontal', 'vertical']),
};

export default Divider;
